const onScroll = () => {

const $scrollhack = document.querySelector(`.scrollhack`);

  if (scrollY > 180) {
    $scrollhack.style.position = `fixed`;
    $scrollhack.style.top = `3rem`;
  } else {
    $scrollhack.style.position = `absolute`;
    $scrollhack.style.top = `21rem`;
  }
};

const triggerOnScroll = () => {
  const $back = document.querySelector(`.back`);

  if (window.innerWidth > 575) {
    $back.classList.Add(`scrollhack`);
    window.addEventListener(`scroll`, onScroll);
  } else {
    $back.classList.Remove(`scrollhack`);
  }
};

const init = () => {

  window.addEventListener(`resize`, triggerOnScroll);

  // render(
  //   <Provider {...stores}>
  //     <Router>
  //       <Route component={App} />
  //     </Router>
  //   </Provider>,
  //   document.querySelector(`.react-mount`)
  // );

};

init();



